import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'CouponProject';

  userType: string = sessionStorage.getItem('userType');
  admin: boolean = false;
  company: boolean = false;
  customer: boolean = false;

  constructor(){}

  ngOnInit(): void {
    this.checkUser()
  }

  public checkUser() {
    this.userType = sessionStorage.getItem('userType')
    switch (this.userType) {
      case 'admin':
        this.admin = true
        break;

      case 'company':
        this.company = true
        break;

      case 'customer':
        this.customer = true
        break;

      default:
        this.admin = false;
        this.company = false;
        this.customer = false;
        break;
    }
  }

}
