import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Company } from '../model/company';
import { Customer } from '../model/customer';

@Injectable({
  providedIn: 'root'
})
export class AdminService {

  constructor(private client:HttpClient) { }

  createCompany(token:string, company:Company){
    return this.client.post<Company>("http://localhost:8080/admin/create/company/" + token, company);
  }

  updateCompany(token:string, company:Company){
    return this.client.put<Company>("http://localhost:8080/admin/update/company/" + token, company);
  }

  deleteCompany(token:string, id:number){
    return this.client.delete("http://localhost:8080/admin/delete/company/" + id + "/" + token, {responseType: 'text'});
  }

  getAllCompanies(token:string){
    return this.client.get<Company[]>("http://localhost:8080/admin/find/all/companies/" + token);
  }

  getOneCompany(token:string, id:number){
    return this.client.get<Company>("http://localhost:8080/admin/find/one/company/" + id + "/" + token);
  }

  createCustomer(token:string, customer:Customer){
    return this.client.post<Customer>("http://localhost:8080/admin/create/customer/" + token, customer);
  }

  updateCustomer(token:string, customer:Customer){
    return this.client.put<Customer>("http://localhost:8080/admin/update/customer/" + token, customer);
  }

  deleteCustomer(token:string, id:number){
    return this.client.delete("http://localhost:8080/admin/delete/customer/" + id + "/" + token, {responseType: 'text'});
  }

  getAllCustomers(token:string){
    return this.client.get<Customer[]>("http://localhost:8080/admin/find/all/customers/" + token);
  }

  getOneCustomer(token:string, id:number){
    return this.client.get<Customer>("http://localhost:8080/admin/find/one/customer/" + id + "/" + token);
  }

}
