import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/model/coupon';
import { CouponLoaderService } from 'src/app/service/coupon-load.service';
import { LoginService } from 'src/app/service/login.service';
import { CustomerService } from 'src/app/service/customer.service';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.css']
})
export class HomepageComponent implements OnInit {

  coupons:Coupon[];
  toBuyCoupon: Coupon;
  userType: string = sessionStorage.getItem('userType');
  customer: boolean = false;

  constructor(private coupService:CouponLoaderService, private loginService:LoginService, private customerService:CustomerService, private snack:MatSnackBar) { }

  ngOnInit(): void {
    this.coupService.getAllCoupons().subscribe((coupons)=>{
      this.coupons = coupons
    });

    this.checkUser()
  }

  public purchaseCoupon(coupon) {
    this.toBuyCoupon = coupon
    this.customerService.purchaseCoupon(sessionStorage.token, coupon).subscribe((coupon)=>{
      this.snack.open("Coupon purchased successfully", "", {duration:2000})
    }, (err)=>{
      console.log(err)
      this.snack.open(err, "", {duration:2000})
    });
  }

  public checkUser() {
    if (this.userType === 'customer') {
    this.customer = true;
    } else {
      this.customer = false;
    }
  }
}
