import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Coupon } from '../model/coupon';
import { CategoryType } from '../enum/category-type.enum';
import { Company } from '../model/company';
import { Session } from 'protractor';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CompanyService {

  constructor(private client:HttpClient) { }

  createCoupon(token:string, coupon:Coupon){
    return this.client.post<Coupon>("http://localhost:8080/company/create/coupon/" + token, coupon);
  }

  updateCoupon(token:string, coupon:Coupon){
    return this.client.put<Coupon>("http://localhost:8080/company/update/coupon/" + token, coupon,);
  }

  deleteCoupon(token:string, id:number){
    return this.client.delete("http://localhost:8080/company/delete/coupon/" + id + "/" + token, {responseType: 'text'});
  }

  findAllCoupons(token:string){
    return this.client.get<Coupon[]>("http://localhost:8080/company/find/all/coupons/" + token);
  }

  findOneCoupon(token:string, id:number){
    return this.client.get<Coupon>("http://localhost:8080/company/find/one/coupon/" + id + "/" + token);
  }

  findAllCouponsByCategory(token:string, category:CategoryType){
    return this.client.get<Coupon[]>("http://localhost:8080/company/find/all/coupons/category/" + category + "/" + token);
  }

  findAllCouponsByPrice(token:string, price:number){
    return this.client.get<Coupon[]>("http://localhost:8080/company/find/all/coupons/price/" + price + "/" + token);
  }

  companyDetails(token:string){
    return this.client.get<Company>("http://localhost:8080/company/info/" + token);
  }

}
