import { Component, OnInit } from '@angular/core';
import { Customer } from 'src/app/model/customer';
import { CustomerService } from 'src/app/service/customer.service';
import { Coupon } from 'src/app/model/coupon';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'app-customer',
  templateUrl: './customer.component.html',
  styleUrls: ['./customer.component.css']
})
export class CustomerComponent implements OnInit {

  customer: Customer;
  coupons: Coupon[];
  filterCoupons: Coupon[];

  categoryFilter: FormControl;
  priceFilter: FormControl;
  maxPrice: FormControl;
  categoryType: FormControl;

  constructor(private customerService:CustomerService) { }

  ngOnInit(): void {
    this.customerService.findCustomerCoupons(sessionStorage.token).subscribe((coupons)=>{
      this.coupons = coupons;
    });

    this.categoryFilter = new FormControl("");
    this.priceFilter = new FormControl("");
    this.maxPrice = new FormControl("");
    this.categoryType = new FormControl("");
  }

  // FILTER BY MAX PRICE
  public getCouponsByMaxPrice() {
    this.customerService.findCustomerCouponsByPrice(sessionStorage.token, this.maxPrice.value).subscribe((result)=>{
      this.filterCoupons = result;
    });
  }

  // FILTER BY CATEGORY
  public getCouponsByCategory() {
    this.customerService.findCustomerCouponsByCategory(sessionStorage.token, this.categoryType.value).subscribe((result)=>{
      this.filterCoupons = result;
    });
  }

  public showDetails() {
    this.customerService.customerDetails(sessionStorage.token).subscribe((customer)=>{
      this.customer = customer
    }, (err)=>{
      console.log(err)
    });
  }

}
