import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Coupon } from '../model/coupon';
import { CategoryType } from '../enum/category-type.enum';
import { Customer } from '../model/customer';

@Injectable({
  providedIn: 'root'
})
export class CustomerService {

  constructor(private client:HttpClient) { }

  purchaseCoupon(token:string, coupon:Coupon){
    return this.client.post<Coupon>("http://localhost:8080/customer/purchase/" + token, coupon);
  }

  findCustomerCoupons(token:string){
    return this.client.get<Coupon[]>("http://localhost:8080/customer/find/all/coupons/" + token);
  }

  findCustomerCouponsByCategory(token:string, category:CategoryType){
    return this.client.get<Coupon[]>("http://localhost:8080/customer/find/all/coupons/category/" + category + "/" + token);
  }

  findCustomerCouponsByPrice(token:string, price:number){
    return this.client.get<Coupon[]>("http://localhost:8080/customer/find/all/coupons/price/" + price + "/" + token);
  }

  customerDetails(token:string){
    return this.client.get<Customer>("http://localhost:8080/customer/info/" + token);
  }

}
